<template>
  <div class="flex">
    <aside>
      <el-scrollbar wrap-class="scrollbar-wrapper" view-class="view-box">
        <ul>
          <li class="flex" v-for="(item,i) in total" :key="i">
            <img src="@/assets/logo.png" alt="">
            <div class="info">
              <h2>夏江</h2>
              <p>石油工程院士</p>
            </div>
          </li>
        </ul>
      </el-scrollbar>
    </aside>
    <section>
      <div class="container">
          <div class="imgbox">
              <img src="@/assets/bg.png" alt="">
          </div>
          <div class="msgbox">
              <h1>夏双江</h1>
              <h6>武汉建设三局 设计施工部</h6>
              <div class="msg">
                <p>工号：</p>
                <p class="rt">002676</p>
              </div>
              <div class="msg">
                <p>职称：</p>
                <p class="rt">高级工程师</p>
              </div>
              <div class="msg">
                <p>专业：</p>
                <p class="rt">石油侦查</p>
              </div>
              <div class="msg">
                <p>工作年限：</p>
                <p class="rt">13年</p>
              </div>
              <div class="msg">
                <p>手机号：</p>
                <p class="rt">13212565874</p>
              </div>
          </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data () {
    return {
      total: 8,
      list: [
        {
          url: '',
          name: '',
          level: ''
        }
      ]
    }
  },

  components: {},

  computed: {},

  mounted: function() {},

  methods: {}
}
</script>

<style lang='scss' scoped>
.flex {
  height: 100%;
}
aside {
  width: 260px;
  background: #304156;
  li {
    align-items: center;
    padding: 10px 20px;
    color: #fff;
    img {
      width: 100px;
      height: 80px;
    }
  }
}
section {
  width: calc(100% - 260px);
  .container {
    width: 98%;
    height: calc(100% - 40px);
    padding: 30px 30px 30px 150px;
    margin: 20px auto;
    background: #fff;
    box-sizing: border-box;
  }
  .container .imgbox{
    width: 210px;
    height: 210px;
    float: left;
  }
  .container .imgbox img{
    width: 100%;
    height: 100%;
  }
  .container .msgbox{
    width: 400px;
    float: left;
    overflow: hidden;
    margin-left: 120px;
  }
  .container .msgbox h1,.container .msgbox h6{
    font-size: 15px;
    color: #333333;
    line-height: 24px;
    margin: 0;
    left: 0;
  }
  .container .msgbox h6{
    font-weight: normal;
    margin-bottom: 10px;
  }
  .container .msgbox .msg{
    width: 376px;
    height: 53px;
    line-height: 53px;
    padding: 0 12px;
    border-bottom: 1px solid #e5e5e5;
  }
  .container .msgbox .msg p{
    font-size: 16px;
    color: #333333;
    float: left;
    margin: 0;
  }
  .container .msgbox .msg p.rt{
    float: right;
  }










}









</style>



